// 
export enum UnitOfMeasure {
  KILOGRAMS = "KILOGRAMS",
  CENTIMETERS = "CENTIMETERS",
  SQUARE_METERS = "SQUARE_METERS",
  LITERS = "LITERS",
}

export enum Color {
  RED = "RED",
  ORANGE = "ORANGE",
  GREEN = "GREEN",
  WHITE = "WHITE",
  BROWN = "BROWN",
}

export enum Country {
  RUSSIA = "RUSSIA",
  INDIA = "INDIA",
  VATICAN = "VATICAN",
  SOUTH_KOREA = "SOUTH_KOREA",
}

//  
export interface Coordinates {
  x: number;
  y: number;
}

export interface Location {
  id: number;
  x: number;
  y: number;
  z: number;
}

export interface Address {
  id: number;
  zipCode?: string; //    null
  town?: Location | undefined; //    null
}

export interface AddressWithExtraFields extends Address {
  createTown?: Omit<Location, "id">; //    id
  linkTownId?: number | null;
}

//    Person
export interface Person {
  id: number;
  name: string;
  birthday?: Date; //    null
  hairColor: Color;
  eyeColor?: Color; //  ,   null
  nationality: Country;
  location: Location | undefined; // , 
}

export interface PersonWithExtraFields extends Person {
  createLocation?: Omit<Location, "id">; //    id
  linkLocationId?: number | null; // ID  
}

//    Organization
export interface Organization {
  id: number;
  name: string;
  officialAddress?: Address; //   null
  annualTurnover?: number; //   null
  employeesCount: number;
  rating?: number; //   null
  postalAddress?: Address; //   null
  fullName?: string;
}

export interface OrganizationWithExtraFields extends Organization {
  createOfficialAddress?: Omit<AddressWithExtraFields, "id">; //    
  linkOfficialAddressId?: number | null; //    
  createPostalAddress?: Omit<AddressWithExtraFields, "id">; //    
  linkPostalAddressId?: number | null; //    
}

//     Product
export interface Product {
  id: number;
  name: string;
  coordinates: Coordinates;
  creationDate: Date; //   
  unitOfMeasure: UnitOfMeasure;
  manufacturer: Organization;
  price: number;
  manufactureCost?: number | null; //   null
  rating: number;
  partNumber: string;
  owner: Person;
}

//   User
export interface User {
  username: string;
  role: "USER" | "ADMIN";
  approved: boolean;
  products: Product[] | null;
  id: number;
  jwt: string;
}
