import * as yup from "yup";
import { toast } from "react-toastify";
import { makeAutoObservable, runInAction } from "mobx";
import {
  fetchOrganizations,
  createOrganization,
  updateOrganization,
  deleteOrganization,
} from "../api/organizations";
import { Organization } from "../types/types";

//     Organization
const organizationSchema = yup.object().shape({
  name: yup
    .string()
    .required("  ")
    .min(1, "    "),
  employeesCount: yup
    .number()
    .required("  ")
    .moreThan(0, "     0"),
  annualTurnover: yup
    .number()
    .nullable()
    .moreThan(0, "     0")
    .notRequired(),
  rating: yup
    .number()
    .nullable()
    .moreThan(0, "    0")
    .notRequired(),
  fullName: yup.string().nullable(),
});

export class OrganizationStore {
  organizations: Organization[] = []; //   
  loading: boolean = false; //  
  error: string | null = null; //      null

  constructor() {
    makeAutoObservable(this);
  }

  //  
  resetError(): void {
    this.error = null;
  }

  //    
  async loadOrganizations(): Promise<void> {
    this.loading = true;
    this.resetError();
    try {
      const data = await fetchOrganizations();
      runInAction(() => {
        this.organizations = data;
      });
    } catch (error) {
      runInAction(() => {
        this.error = "  ";
      });
      toast.error("  ");
    } finally {
      runInAction(() => {
        this.loading = false;
      });
    }
  }

  //  
  async createOrganization(org: Omit<Organization, "id">): Promise<void> {
    this.resetError();
    try {
      //   
      await organizationSchema.validate(org, { abortEarly: false });

      const newOrganization = await createOrganization(org);
      runInAction(() => {
        this.organizations.push(newOrganization);
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "  ";
        });
        toast.error("  ");
      }
    }
  }

  //  
  async updateOrganization(
    id: number,
    updatedOrg: Organization
  ): Promise<void> {
    this.resetError();
    try {
      //   
      await organizationSchema.validate(updatedOrg, { abortEarly: false });

      const updatedOrganization = await updateOrganization(id, updatedOrg);
      runInAction(() => {
        this.organizations = this.organizations.map((org) =>
          org.id === id ? updatedOrganization : org
        );
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "  ";
        });
        toast.error("  ");
      }
    }
  }

  //  
  async deleteOrganization(id: number): Promise<void> {
    this.resetError();
    try {
      await deleteOrganization(id);
      runInAction(() => {
        this.organizations = this.organizations.filter((org) => org.id !== id);
      });
      toast.success("  ");
    } catch (error) {
      runInAction(() => {
        this.error = "  ";
      });
      toast.error("  ");
    }
  }
}

export const organizationStore = new OrganizationStore();
