import * as yup from "yup";
import { toast } from "react-toastify";
import { makeAutoObservable, runInAction } from "mobx";
import {
  fetchPersons,
  createPerson,
  updatePerson,
  deletePerson,
} from "../api/persons";
import { Person } from "../types/types";

//    Person
const personSchema = yup.object().shape({
  name: yup
    .string()
    .required(" ")
    .min(1, "    "),
  birthday: yup.date().nullable().notRequired(),
  hairColor: yup
    .string()
    .required("  ")
    .oneOf(
      ["RED", "GREEN", "ORANGE", "WHITE", "BROWN"],
      "  "
    ),
  eyeColor: yup
    .string()
    .nullable()
    .oneOf(
      ["RED", "GREEN", "ORANGE", "WHITE", "BROWN"],
      "  "
    )
    .notRequired(),
  nationality: yup
    .string()
    .required(" ")
    .oneOf(
      ["RUSSIA", "INDIA", "VATICAN", "SOUTH_KOREA"],
      " "
    ),
  location: yup
    .object()
    .shape({
      id: yup.number().required("ID  "),
      x: yup.number().required(" X "),
      y: yup.number().required(" Y "),
      z: yup.number().required(" Z "),
    })
    .required(" "),
});

export class PersonStore {
  persons: Person[] = [];
  loading: boolean = false;
  error: string | null = null;

  constructor() {
    makeAutoObservable(this);
  }

  //  
  resetError(): void {
    this.error = null;
  }

  //  
  async loadPersons(): Promise<void> {
    this.loading = true;
    this.resetError();
    try {
      const data = await fetchPersons();
      runInAction(() => {
        this.persons = data;
      });
    } catch (error) {
      runInAction(() => {
        this.error = "  ";
      });
      toast.error("  ");
    } finally {
      runInAction(() => {
        this.loading = false;
      });
    }
  }

  //  
  async createPerson(person: Omit<Person, "id">): Promise<void> {
    this.resetError();
    try {
      //   
      await personSchema.validate(person, { abortEarly: false });

      const newPerson = await createPerson(person);
      runInAction(() => {
        this.persons.push(newPerson);
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "   ";
        });
        toast.error("   ");
      }
    }
  }

  //  
  async updatePerson(id: number, person: Omit<Person, "id">): Promise<void> {
    this.resetError();
    try {
      //   
      await personSchema.validate(person, { abortEarly: false });

      const updatedPerson = await updatePerson(id, person);
      runInAction(() => {
        this.persons = this.persons.map((p) =>
          p.id === id ? updatedPerson : p
        );
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "   ";
        });
        toast.error("   ");
      }
    }
  }

  //  
  async deletePerson(id: number): Promise<void> {
    this.resetError();
    try {
      await deletePerson(id);
      runInAction(() => {
        this.persons = this.persons.filter((p) => p.id !== id);
      });
      toast.success("  ");
    } catch (error) {
      runInAction(() => {
        this.error = "   ";
      });
      toast.error("   ");
    }
  }
}

export const personStore = new PersonStore();
