import axiosInstance from "../instance/axiosInstance";
import { Organization } from "../types/types";

//   
export const fetchOrganizations = async (): Promise<Organization[]> => {
  try {
    const response = await axiosInstance.get<Organization[]>("/organizations");
    return response.data;
  } catch (error) {
    console.error("    :", error);
    throw error;
  }
};

//  
export const createOrganization = async (
  organization: Omit<Organization, "id">
): Promise<Organization> => {
  try {
    const response = await axiosInstance.post<Organization>(
      "/organizations",
      organization
    );
    return response.data;
  } catch (error) {
    console.error("   :", error);
    throw error;
  }
};

//  
export const updateOrganization = async (
  id: number,
  organization: Organization
): Promise<Organization> => {
  try {
    const response = await axiosInstance.put<Organization>(
      `/organizations/${id}`,
      organization
    );
    return response.data;
  } catch (error) {
    console.error("   :", error);
    throw error;
  }
};

//  
export const deleteOrganization = async (id: number): Promise<void> => {
  try {
    await axiosInstance.delete(`/organizations/${id}`);
  } catch (error) {
    console.error("   :", error);
    throw error;
  }
};
