import * as yup from "yup";
import { toast } from "react-toastify";
import { makeAutoObservable } from "mobx";
import { Location } from "../types/types";
import {
  fetchLocations,
  createLocation,
  updateLocation,
  deleteLocation,
} from "../api/location";

//     Location
const locationSchema = yup.object().shape({
  x: yup
    .number()
    .required(" X ")
    .moreThan(-947, " X    -947"),
  y: yup
    .number()
    .required(" Y ")
    .max(903, " Y     903"),
  z: yup.number().required(" Z "),
});

class LocationStore {
  locations: (Location & { id: number })[] = [];
  error: string | null = null;

  constructor() {
    makeAutoObservable(this);
  }

  async loadLocations() {
    try {
      this.locations = await fetchLocations();
      this.error = null;
    } catch (error) {
      console.error("  :", error);
      this.error = "   .";
      toast.error("  ");
    }
  }

  async createLocation(location: Omit<Location, "id">) {
    try {
      //   
      await locationSchema.validate(location, { abortEarly: false });

      const newLocation = await createLocation(location);
      this.locations.push(newLocation);
      this.error = null;
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        console.error("  :", error);
        this.error = "   .";
        toast.error("  ");
      }
    }
  }

  async updateLocation(id: number, location: Location) {
    try {
      //   
      await locationSchema.validate(location, { abortEarly: false });

      const updatedLocation = await updateLocation(id, location);
      this.locations = this.locations.map((l) =>
        l.id === id ? updatedLocation : l
      );
      this.error = null;
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        console.error("  :", error);
        this.error = "   .";
        toast.error("  ");
      }
    }
  }

  async deleteLocation(id: number) {
    try {
      await deleteLocation(id);
      this.locations = this.locations.filter((l) => l.id !== id);
      this.error = null;
      toast.success("  ");
    } catch (error) {
      console.error("  :", error);
      this.error = "   .";
      toast.error("  ");
    }
  }
}

export const locationStore = new LocationStore();
