import React, { useEffect, useState } from "react";
import { Button } from "./ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { fetchPendingAdmins, approveAdmin } from "../api/adminApproval";
import { User } from "../types/types";

const AdminApprovalPage: React.FC = () => {
  const [pendingAdmins, setPendingAdmins] = useState<User[]>([]);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const loadPendingAdmins = async () => {
      try {
        const admins = await fetchPendingAdmins();
        setPendingAdmins(admins);
      } catch (e) {
        setError("   .");
      }
    };
    loadPendingAdmins();
  }, []);

  const handleApprove = async (id: number) => {
    try {
      await approveAdmin(id, true);
      setPendingAdmins((prev) => prev.filter((admin) => admin.id !== id));
    } catch (e) {
      setError("  .");
    }
  };

  const handleReject = async (id: number) => {
    try {
      await approveAdmin(id, false);
      setPendingAdmins((prev) => prev.filter((admin) => admin.id !== id));
    } catch (e) {
      setError("  .");
    }
  };

  return (
    <div className='container mx-auto p-4'>
      <h1 className='text-2xl font-bold mb-4'>
          
      </h1>
      {error && <p className='text-red-500'>{error}</p>}
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead> </TableHead>
            <TableHead></TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {pendingAdmins.map((admin) => (
            <TableRow key={admin.id}>
              <TableCell>{admin.id}</TableCell>
              <TableCell>{admin.username}</TableCell>
              <TableCell>
                <Button
                  className='mr-4'
                  onClick={() => handleApprove(admin.id)}
                >
                  
                </Button>
                <Button
                  onClick={() => handleReject(admin.id)}
                  variant='destructive'
                >
                  
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default AdminApprovalPage;
