import * as yup from "yup";
import { toast } from "react-toastify";
import { makeAutoObservable, runInAction } from "mobx";
import { Product } from "../types/types";
import * as productApi from "../api/products";

//    Product
const productSchema = yup.object().shape({
  name: yup
    .string()
    .required("  ")
    .min(1, "    "),
  coordinates: yup
    .object()
    .shape({
      x: yup.number().required(" X ").moreThan(-947),
      y: yup.number().required(" Y ").max(903),
    })
    .required(" "),
  creationDate: yup.date().notRequired(),
  unitOfMeasure: yup
    .string()
    .required("  ")
    .oneOf(
      ["KILOGRAMS", "CENTIMETERS", "SQUARE_METERS", "LITERS"],
      "  "
    ),
  manufacturer: yup
    .object()
    .shape({
      id: yup.number().required("ID  "),
      name: yup.string().required("  "),
    })
    .required(" "),
  price: yup
    .number()
    .required(" ")
    .moreThan(0, "    0"),
  manufactureCost: yup.number().nullable(),
  rating: yup
    .number()
    .required(" ")
    .moreThan(0, "    0"),
  partNumber: yup
    .string()
    .required("Part Number ")
    .min(15, "Part Number     15 "),
  owner: yup
    .object()
    .shape({
      id: yup.number().required("ID  "),
      name: yup.string().required("  "),
    })
    .required(" "),
});

class ProductStore {
  products: Product[] = [];
  selectedProduct: Product | null = null;
  loading: boolean = false;
  error: string | null = null;

  constructor() {
    makeAutoObservable(this);
  }

  //  
  resetError() {
    this.error = null;
  }

  //    
  async loadProducts() {
    this.loading = true;
    this.resetError();
    try {
      const data = await productApi.fetchProducts();
      runInAction(() => {
        this.products = data.map((product: Product) => ({
          ...product,
          creationDate: new Date(product.creationDate),
        }));
      });
    } catch (error) {
      runInAction(() => {
        this.error = "   ";
      });
      toast.error("   ");
    } finally {
      runInAction(() => {
        this.loading = false;
      });
    }
  }

  //  
  async createProduct(product: Product) {
    this.resetError();
    try {
      //   
      await productSchema.validate(product, { abortEarly: false });

      const newProduct = await productApi.createProduct(product);
      runInAction(() => {
        this.products.push(newProduct);
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "   ";
        });
        toast.error("   ");
      }
    }
  }

  //  
  async updateProduct(id: number, updatedProduct: Product) {
    this.resetError();
    try {
      //   
      await productSchema.validate(updatedProduct, { abortEarly: false });

      const product = await productApi.updateProduct(id, updatedProduct);
      runInAction(() => {
        this.products = this.products.map((p) => (p.id === id ? product : p));
      });
      toast.success("  ");
    } catch (error) {
      if (error instanceof yup.ValidationError) {
        toast.error(` : ${error.errors.join(", ")}`);
      } else {
        runInAction(() => {
          this.error = "   ";
        });
        toast.error("   ");
      }
    }
  }

  //  
  async deleteProduct(id: number) {
    this.resetError();
    try {
      await productApi.deleteProduct(id);
      runInAction(() => {
        this.products = this.products.filter((p) => p.id !== id);
      });
      toast.success("  ");
    } catch (error) {
      runInAction(() => {
        this.error = "   ";
      });
      toast.error("   ");
    }
  }

  //   
  setSelectedProduct(product: Product | null) {
    this.selectedProduct = product;
  }
}

export const productStore = new ProductStore();
